import React from 'react';
import { Input } from 'antd';
import P from '../../re-use/p/P';
import { ErrorMessage } from 'formik';
import classes from '../../sign-up/name/Name.module.scss';
function Email(props) {
	const handleErr = () => {
		if (props.props.touched.email && props.props.errors.email) {
			return 'border border-danger';
		} else {
			return '';
		}
	};
	const cls = handleErr();
	return (
		<div>
			<Input
				style={{ direction: 'rtl' }}
				onBlur={() => props.props.setFieldTouched('email')}
				onChange={(e) => props.props.setFieldValue('email', e.target.value)}
				placeholder={`ایمیل`}
				className={`${classes.name} ${cls}`}
			/>
			<P class="p-0 m-0 text-danger mt-2">
				<ErrorMessage name="email" />
			</P>
		</div>
	);
}

export default Email;
