import React from 'react';
import { Input } from 'antd';
import { ErrorMessage } from 'formik';
import classes from '../../sign-up/name/Name.module.scss';

import P from '../../re-use/p/P';

function Password(props) {
	const handleErr = () => {
		if (props.props.touched.password && props.props.errors.password) {
			return 'border border-danger';
		} else {
			return '';
		}
	};
	const cls = handleErr();
	return (
		<div>
			<Input
				style={{ direction: 'rtl' }}
				onBlur={() => props.props.setFieldTouched('password')}
				onChange={(e) => props.props.setFieldValue('password', e.target.value)}
				props={props}
				placeholder={`رمز حساب`}
				className={`${classes.name} ${cls}`}
				type="password"
			/>
			<P class="p-0 m-0 text-danger mt-2">
				<ErrorMessage name="password" />
			</P>
		</div>
	);
}

export default Password;
