import React from 'react';
import { Formik, Form } from 'formik';
import Axios from 'axios';
import * as yup from 'yup';
import { withRouter } from 'react-router-dom';

// Components
import SubmitBtn from '../add/submit-btn/SubmitBtn';
import BootstrapContainer from '../re-use/bootstrap-container/BootstrapContainer';
import AddContainer from '../re-use/add/AddContainer';
import AddItemContainer from '../re-use/add/AddItemContainer';
import Email from './email/Email';
import Password from './password/Password';
import PageLink from '../re-use/page-link/PageLink';
import P from '../re-use/p/P';

const initialValues = {
	email: '',
	password: ''
};

const validationSchema = yup.object({
	email: yup.string().typeError('ایمیل وارد نشده').email('ایمیل درست وارد نشده').required('ایمیل وارد نشده'),
	password: yup
		.string()
		.typeError('رمزحساب وارد نشده')
		.max(10, 'رمزحساب باید کمتر از 10 حروف باشد')
		.required('رمزحساب انتخاب نشده')
});

function Login(props) {
	const [ loading, setLoading ] = React.useState(false);
	const [ err, setErr ] = React.useState('');
	const onSubmit = (e) => {
		setLoading(true);
		Axios.post('https://ravid-app.herokuapp.com/loginUser', e)
			.then((res) => {
				localStorage.setItem('id', res.data.id);
				localStorage.setItem('token', res.data.token);
				localStorage.setItem('number', res.data.number);
			})
			.then(() => {
				setLoading(false);
				props.history.push('/');
			})
			.catch(() => {
				setLoading(false);
				setErr('ایمیل یا پسورد اشتباه است');
			});
	};

	return (
		<Formik initialValues={initialValues} onSubmit={onSubmit} validationSchema={validationSchema}>
			{(props) => {
				return (
					<Form>
						<BootstrapContainer class="mt-5">
							<AddContainer>
								<AddItemContainer class={`align-items-center`}>
									<Email props={props} />
								</AddItemContainer>
							</AddContainer>
							<AddContainer>
								<AddItemContainer class={`align-items-center`}>
									<Password props={props} />
								</AddItemContainer>
							</AddContainer>
						</BootstrapContainer>
						<SubmitBtn loading={loading} props={props} text={`ورود به حساب`} />
						{err.length ? <P class="text-center text-danger mt-3">{err}</P> : null}
						<PageLink text={`حساب کاربری ندارید ؟`} link={`ایجاد حساب`} to="/signup" />
					</Form>
				);
			}}
		</Formik>
	);
}

export default withRouter(Login);
