import React from 'react';
import P from '../p/P';
import { Link } from 'react-router-dom';

function PageLink(props) {
	return (
		<React.Fragment>
			<P class="text-center mt-3">
				{props.text}
				<Link to={props.to}> {props.link} </Link>
			</P>
		</React.Fragment>
	);
}

export default PageLink;
